:root {
  --font-family-main: "Montserrat", sans-serif;
  --pimary-text-color: #b9b9b9;
  --secondary-text-color: #fff;
  --third-text-color: #3d3d3d;
  --title-text-color: #111518;
  --accent-color: #ff6b09;
  --valid-color: #0f0;
  --invalid-color: red;
  --primary-background-color: #fff;
  --secondary-background-color: #e5e5e5;
  --third-background-color: #111518;
  --fourth-background-color: #f1f1f1;
  --fifth-background-color: #3d3d3d;
  --shift: 250ms cubic-bezier(.4, 0, .2, 1);
}

html {
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
}

main {
  display: block;
}

h1 {
